import React, { useState } from "react";
import { json, NavLink ,useNavigate} from "react-router-dom";

const Register = () => {

  const navigate = useNavigate()

  const [inpVal, setINP] = useState({
    name: "",
    email: "",
    age: "",
    mobile: " ",
    work: "",
    add: "",
    desc: "",
  });
  console.log(inpVal)

  const setData = (e) => {
    console.log(e.target.value);
    const { name, value } = e.target;
    setINP((preVal) => {
      return {
        ...preVal,
        [name]: value,
      };
    });
  };

  const addInpData = async (e) => {
    e.preventDefault();

    const { name, email, work, add, mobile, desc, age } = inpVal;
    console.log(inpVal)
  const body =  {
        name:name,
        email:email,
        work:work,
        add:add,
        mobile:mobile,
        desc:desc,
        age:age
      }
    const res = await fetch("/register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body)
    });
    const data = await res.json();
    console.log(res);

    if (data.status === 422 || !data) {
      alert(data.message);
      console.log("error ");
      alert('data not added')
     
    } 
    else {
      setData(e);
      alert("Data added");
      console.log("data added");
      navigate('/')
    }
  };
  return (
    <div className="container">
      <NavLink to="/">Home</NavLink>
      <form className="mt-4">
        <div className="row">
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputEmail1" className="form-label">
              Name
            </label>
            <input
              name="name"
              type="text"
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              value={inpVal.name || ""}
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              email
            </label>
            <input
              value={inpVal.email|| ""}
              name="email"
              type="email"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Age
            </label>
            <input
              name="age"
              value={inpVal.age|| ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>

          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Mobile
            </label>
            <input
              name="mobile"
              value={inpVal.mobile|| ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              work
            </label>
            <input
              name="work"
              value={inpVal.work|| ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              address
            </label>
            <input
              name="add"
              value={inpVal.add|| ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3 col-lg-12 col-md-12 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Description
            </label>
            <textarea
              name="desc"
              value={inpVal.desc|| ""}
              onChange={setData}
              className="form-control"
              id=""
              cols="30"
              rows="5"
            ></textarea>
          </div>

          <button
            onClick={addInpData}
            type="submit"
            className="btn btn-primary"
          >
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};

export default Register;
