import React, { useState, useEffect,useContext } from "react";
import { NavLink } from "react-router-dom";
import { useParams } from "react-router-dom";
import {useNavigate} from 'react-router-dom';
import { updatedata } from './context/ContextProvider'




const Edit = () => {
  // const [getUserData,setUserData]= useState([])
  // console.log(getUserData)

  const {updata, setUPdata} = useContext(updatedata)

  const navigate = useNavigate()

  const [inpVal, setINP] = useState({
    name: "",
    email: "",
    age: "",
    mobile: " ",
    work: "",
    add: "",
    desc: ""
  });

  const setData = (e) => {
    console.log(e.target.value);
    const { name, value } = e.target;
    setINP((preVal) => {
      return {
        ...preVal,
        [name]: value,
      };
    });
  };

  const { id } = useParams("");
  console.log(id);

  const getData = async () => {
    const res = await fetch(`/getUser/${id}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    });
    const data = await res.json();
    console.log(data);
    if (res.status === 422 || !data) {
      console.log("error");
    } else {
      setINP(data);
      console.log("get data");
    }
  };
  useEffect(() => {
    getData();
  }, []);

  const updateUser = async (e) => {
    e.preventDefault();
    const { name, email, work, add, mobile, desc, age } = inpVal;
    const body =  {
      name:name,
      email:email,
      work:work,
      add:add,
      mobile:mobile,
      desc:desc,
      age:age
    }
    console.log(inpVal);

    const res2 = await fetch(`/updateUser/${id}`, {
      method: "PATCH",
      header: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    });

    const data2 = await res2.json();
    console.log(data2);
    if (res2.status === 422 || !data2) {
      alert("plz update date");
    } else {
      alert("data added");
      navigate('/')
      setUPdata(data2);
    }
  };

  return (
    <div className="container">
      <NavLink to="/">Home 2</NavLink>
      <form className="mt-4">
        <div className="row">
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputEmail1" className="form-label">
              Name
            </label>
            <input
              name="name"
              type="text"
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              value={inpVal.name || ""}
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              email
            </label>
            <input
              value={inpVal.email || ""}
              name="email"
              type="email"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Age
            </label>
            <input
              name="age"
              value={inpVal.age || ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>

          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Mobile
            </label>
            <input
              name="mobile"
              value={inpVal.mobile || ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              work
            </label>
            <input
              name="work"
              value={inpVal.work || ""} 
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3col-lg-6 col-md-6 col-12">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Address
            </label>
            <input
              name="address"
              value={inpVal.add || ""}
              type="text"
              className="form-control"
              id="exampleInputPassword1"
              onChange={setData}
            />
          </div>
          <div className="mb-3 ">
            <label htmlFor="exampleInputPassword1" className="form-label">
              description
            </label>
            <textarea
              name="description"
              id=""
              cols="30"
              className="form-control"
              rows="5"
              value={inpVal.desc || ""}
              onChange={setData}
            ></textarea>
            {/* <input
          type="password"
          class="form-control"
          id="exampleInputPassword1"
        /> */}
          </div>

          <button
            type="submit"
            onClick={updateUser}
            className="btn btn-primary"
          >
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};

export default Edit;
