import React,{useState,useEffect} from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import CreateIcon from "@mui/icons-material/Create";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import EmailIcon from "@mui/icons-material/Email";
import WorkIcon from "@mui/icons-material/Work";
import SmartphoneIcon from '@mui/icons-material/Smartphone';
import FmdGoodIcon from '@mui/icons-material/FmdGood';
import { useParams,NavLink, Navigate, useNavigate } from "react-router-dom";



const Details = () => {
 
  const [getUserData,setUserData]= useState([])
  console.log(getUserData)
  const navigate = useNavigate();

  const {id} = useParams("");
  console.log(id)

  const getData = async ( ) => {
   
      const res = await fetch(`/getUser/${id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      const data = await res.json();
      console.log(data);
      if(res.status === 422 || !data )
      {
        console.log("error");
        // alert("error");
      }
      else {
        setUserData(data)
        console.log("get data")
      }
    };

    useEffect(() => {
    getData();
      
    },[]);
    

    const deleteUser = async(id)=>{

      const res2 = await fetch(`/deleteUser/${id}`,
      {
        method:"DELETE",
        headers:
        {
          "Content-type":"application/json"
        }
      })
  
     const deleteData = await res2.json();
  console.log(deleteData)
  
  if(res2.status === 422 ){
    console.log("error");
  
  }
  else{
    console.log("user deleted")
   navigate('/')
  }
  
     }
  

  return (
    <div className="container mt-3">
      <h1 style={{ fontWeight: 400 }}> Welcome Aditya</h1>

      <Card sx={{ maxWidth: 600 }}>
        <CardContent>
        <div className="add_btn">

          <NavLink to={`/edit/${getUserData._id}`}><button className="btn btn-primary mx-2">
      <CreateIcon/>
    </button>
    </NavLink>


    
    <button className="btn btn-danger"
    onClick={()=>deleteUser(getUserData._id)}
    >
      <DeleteIcon/>
    </button>
</div>
{/* 
{
  getUserData.map((ele,id)=>{
    return(
      <h1>{ele.name}</h1>
    )
  })
} */}


            <div className="row">
            <div className="left_view col-mg-6 col-md-6 col-12">
            <img src="/image/a.png" style={{ width: 50 }} alt="Profile" />
            <h3 className="mt-3">
              Name:<span style={{ fontweight: 400 }}>{getUserData.name}</span>
            </h3>
            <h3 className="mt-3">
              Age:<span>{getUserData.age}</span>
            </h3>
            <p  className="mt-3">
              <EmailIcon />
              Email:<span>{getUserData.email}</span>
            </p>
            <p className="mt-3">
              <WorkIcon /> Occupation: <span>{getUserData.work}</span>
            </p>
          </div>
          <div className="right_view col-mg-6 col-md-6 col-12">
           
            <p className="mt-4 ">
                <SmartphoneIcon/> Mobile :<span>
{
  getUserData.mobile
}                </span>
            </p>
            <p className="mt-3">
            <FmdGoodIcon/> Location: <span>{getUserData.add}</span>
            </p>
            <p className="mt-3">Description :<span>{getUserData.desc}</span> </p>
          </div>

            </div>
        
        </CardContent>
      </Card>
    </div>
  );
};

export default Details;
